@import 'mixins';

@font-face {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 700;
    src: local('Roboto Condensed Bold'), local('RobotoCondensed-Bold'),
        url('/fonts/roboto-condensed-v18-latin-700.woff2') format('woff2'),
        url('/fonts/roboto-condensed-v18-latin-700.woff') format('woff');
}

* {
    box-sizing: border-box;
}

html {
    margin: 0;
    padding: 0;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
}

body {
    margin: 0;
    padding: 0;
    position: relative;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Roboto Condensed', sans-serif;
}

h1 {
    @include fontsize(28);
    margin: 0;

    @include minwidth(576) {
        @include fontsize(36);
    }

    @include minwidth(992) {
        @include fontsize(56);
    }
}

header {
    align-items: center;
    background-color: #fff;
    border-bottom: 1px solid #ddd;
    box-shadow: 0 0 1rem rgba(0,0,0,.2);
    display: flex;
    height: 60px;
    justify-content: space-between;
    left: 0;
    padding: 0 1rem;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;

    @include minwidth(992) {
        padding: 0 2rem;
    }
}

.header__branding {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1.5rem;
    font-weight: 700;

    a {
        color: #000;
        margin: 0;
        text-decoration: none;
    }
}

.header__cta {
    a {
        text-decoration: none;
    }
}

main {
    align-items: flex-end;
    background-image: url('./images/oskari.jpg');
    background-image: url('./images/oskari.jpg?webp');
    background-position: 75% 0%;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    height: calc(100vh - 60px);
    margin-top: 60px;
    padding: 1rem;
    position: relative;
    width: 100%;
    z-index: 1;

    @include minwidth(992) {
        align-items: flex-start;
        padding: 2rem;
    }
}

main::before {
    background-attachment: fixed;
    background-image: linear-gradient(0deg,rgba(255,255,255,1) 0,rgba(255,255,255,.5) 10%,rgba(255,255,255,0) 40%);
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;

    @include minwidth(992) {
        background-image: linear-gradient(90deg,#fff 0,rgba(255,255,255,.5) 4%,rgba(255,255,255,0) 6%);
    }
}

main::after {
    background-attachment: fixed;
    background-image: linear-gradient(18deg,rgba(255,255,255,.95) 0,rgba(255,255,255,.6) 30%,rgba(255,255,255,0) 55%,rgba(255,255,255,0) 100%);
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;

    @include minwidth(992) {
        background-image: linear-gradient(105deg,rgba(248,248,248,.95) 0,rgba(248,248,248,.8) 32%,rgba(248,248,248,.5) 42%,rgba(248,248,248,0) 55%,rgba(248,248,248,0) 100%);
    }
}

.content {
    position: relative;
    width: 100%;
    z-index: 2;
}

.cta {
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 8px 0 8px 8px;

    @include minwidth(992) {
        background-color: #223388;
        box-shadow: 0 0 16px rgba(0,0,0,.2);
        border-radius: 3px;
        padding: 7px 16px;
        transition: background-color .3s ease, box-shadow .3s ease;
    }

    &:hover {
        @include minwidth(992) {
            background-color: #2233AA;
            box-shadow: 0 0 4px rgba(0,0,0,.2);
        }
    }

    svg {
        width: 18px;
        height: 18px;

        @include minwidth(992) {
            width: 14px;
            height: 14px;
        }
    
        path {
            fill: #000;

            @include minwidth(992) {
                fill: #fff;
            }
        }
    }

    span {
        display: none;

        @include minwidth(992) {
            color: #fff;
            display: block;
            font-weight: 700;
            line-height: 1;
            padding: 2px 0 3px 12px;
        }
    }
}

